body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f9f9f8;
  font-family: "Helvetica";
}

::-webkit-scrollbar { 
  display: none; 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Landing{
    width: 100%;
    height: 100%;
}

.landing.logo {
    position:relative;
    width: 30vmin;
    max-width: 200px;
    margin-bottom: 2vh;
    margin-top: 20vh;
}

@-webkit-keyframes logoRotate {
    0%   {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
    25%  {-webkit-transform:rotate(-30deg);transform:rotate(-30deg);}
    100% {-webkit-transform:rotate(360deg);transform:rotate(360deg);}
}

@keyframes logoRotate {
    0%   {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
    25%  {-webkit-transform:rotate(-30deg);transform:rotate(-30deg);}
    100% {-webkit-transform:rotate(360deg);transform:rotate(360deg);}
}
  
.landing.logo.active {
    -webkit-animation-name: logoRotate;
            animation-name: logoRotate;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}

.landingWrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
    margin-bottom: 20vh;
}


.landingWrapper .landing.item {
    margin: 1vh;
}


.landing.item{
    font-size: 5vmin;
    font-weight: bold;
    font-family: "Helvetica"
}

.landing.item.social{
    margin-top: 2vmin;
    font-size: 7vmin;
    margin: 2vmin;
}

.landing.item:link {
    text-decoration: none;
}

.landing.item:link, .landing.item:visited {
    color: black;
}
  
.landing.item:hover, .landing.item:active {
    color: grey;
}
.WorkItem {
    position: relative;
    background-color: white;
}

.WorkItem:hover .work.image{
    opacity: 0.4;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

@media only screen and (min-width: 768px) {
    .WorkItemDesc {
        position: absolute;
        opacity: 0;
        
    }

    .WorkItem:hover .WorkItemDesc {
        position:relative;
        opacity: 1;
        transition: opacity 0.5s ease;
        -webkit-backface-visibility: hidden;
                backface-visibility: hidden;
    }
}

.worklink:link, .worklink:visited {
    color: black;
    text-decoration: none;
}

.worklink:hover, .worklink:active {
    color: black;
    text-decoration: none;
}


.workImageContainer {
    display: flex; 
    align-items: center;
    height: 100%;
    border: 5px solid black;
    border-bottom: 0px
}

@media only screen and (min-width: 768px) {
    .workImageContainer {
        border-bottom: 5px solid black;
    }
}

.work.image {
    width: 100%;
    height: 100%;
    opacity: 0.8;
    transition: 0.5s ease;
}

.image.landscape {
    display: inline;
    display: initial;
}

.image.portrait {
    display: none;
}

@media only screen and (min-width: 768px) {
    .image.landscape {
        display: none;
    }

    .image.portrait {
        display: inline;
        display: initial;
    }
}

.WorkItemText {
    width: 70%;
    position: absolute;
    top: 60%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);

    background-color: rgba(116, 116, 116, 0.705);
    padding: 2%;

    font-size: 100%;
    font-weight: bold;
    font-family: "Helvetica";

    transition: 0.5s ease;
}
.Work.wrapper {
    display: grid;
    grid-template-columns: repeat(9, 1fr);
    grid-template-rows: auto;
    height:100%
}

.WorkItem {
    grid-column: span 9;
}

@media only screen and (min-width: 768px) {
    .WorkItem {
        grid-column: span 3;
    }
}



.Contact {
    display: flex;
    flex-direction: column;
    justify-items: center;
    justify-content: center;
    align-items: center;
    height: 100vh;
    min-height: 100vmax;
    width: 100%;
}

.contactHeadings {
    width: 60vmin;
    margin-bottom: 3em;
}

.contactTitle{
    font-size: 2em;
}

.contactDesc{
    font-size: 1em;
}

.contactForm {
    display: grid;
    grid-template-columns: repeat(3, 1fr) ;
    grid-template-rows: repeat(3, auto);
    grid-row-gap: 0.5em;
    justify-items: center;
    align-content: center;
}

.contactEmail {
    width: 60vmin;
    max-width: 20em;
    grid-column: span 3;

    padding: 12px 20px;
    box-sizing: border-box;
    border: 2px solid black;
    border-radius: 4px;
}

.contactText {
    width: 60vw;
    max-width: 50em;
    height: 12em;
    max-height: 40vmin;
    grid-column: span 3;

    padding: 12px 20px;
    box-sizing: border-box;
    border: 2px solid black;
    border-radius: 4px;
}

.contactEmail, .contactText {
    font-family: monospace;
    background-color: rgb(255, 255, 255);
}

.contactEmail:focus, .contactText:focus {
    background-color: rgb(250, 250, 250);
}

.contactSend {
    width: 6em;
    height: 3em;
    grid-column: 2;

    font-weight: bold;
    font-family: monospace;
    font-size: 14;

    display: inline-block;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: black;
    background-color: rgb(209, 209, 209);
    border: none;
    border-radius: 15px;
    box-shadow: 0 5px #999;
    margin-bottom: 4em;
}


.contactSend span {
    cursor: pointer;
    display: inline-block;
    position: relative;
    transition: 0.5s;
}

.contactSend:hover span {
    padding-right: 1em;
  }
  

.contactSend span:after {
    position: absolute;
    content: '\BB';
    opacity: 0;
    top: -0.06em;
    right: -1em;
    transition: 0.5s;
}

.contactSend:hover span:after {
    opacity: 1;
    right: 0;
}
  
  
.contactSend:active {
    background-color: rgb(209, 209, 209);
    box-shadow: 0 2px #666;
    -webkit-transform: translateY(4px);
            transform: translateY(4px);
}
.Header {
    position:fixed;
    pointer-events: none;
    z-index:1;
    width: 100%;
    height: 100%;
    transition: background-color 0.5s;
}

.Header.change {
    pointer-events: auto;
    visibility: visible;
    background-color: rgba(190, 190, 190, 0.9);
}


.menu.container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: auto;
    justify-items: center;
    align-content: center;
    margin: auto;
    height: 100%;

    pointer-events: none;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0.3s, opacity 0.3s;
}

.menu.container.change {
    pointer-events: auto;
    visibility: visible;
    opacity: 1;
}

.menu.item {
    grid-column: span 3;
    grid-row: span 1;
    margin: 20px;
}

.navbar {
    padding: 2.5vmax;
    margin: 2.5vmax;
    pointer-events: auto;
}

.navbar.logo {
    width: 40px;
    float:left;
}

.navbar.menuicon{
    float:right;
}

.menuicon {
    display: inline-block;
    cursor: pointer;
}
  
.bar1, .bar2, .bar3 {
    width: 35px;
    height: 5px;
    background-color: #333;
    margin-top: 6px;
    margin-bottom: 6px;
    transition: 0.4s;
}

.change .bar1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px);
    transform: rotate(-45deg) translate(-9px, 6px);
}

.change .bar2 {
    opacity: 0;
}

.change .bar3 {
    -webkit-transform: rotate(45deg) translate(-8px, -8px);
    transform: rotate(45deg) translate(-8px, -8px);
}



.menu.item{
    font-size: 5vmin;
    font-weight: bold;
    font-family: "Helvetica";
    color:black;
}

.menu.item:link, .menu.item:visited {
    color: black;
    text-decoration: none;
}
  
.menu.item:hover, .menu.item:active {
    color: rgb(88, 88, 88);
    text-decoration: none;
}
.App {
  text-align: center;
  height: 100%;
  width: 100%;
}

.page-enter .Contact {
  opacity: 0;
  transition: opacity 1s ease-in-out;

}
.page-enter-active .Contact {
  opacity: 1;
}

.page-enter .landing.item {
  opacity: 0;
  transition: opacity 0.4s ease-in;
  transition-delay: 0.6s;
}

.page-enter-active .landing.item {
  opacity: 1;
}

.page-enter .landing.item.logo {
  position: relative;
  opacity: 1;
  -webkit-transform: translate(calc( 20px + 5vmax - 50vw), 
                        calc( 5vmax - 20vh)) 
              rotate(180deg);
          transform: translate(calc( 20px + 5vmax - 50vw), 
                        calc( 5vmax - 20vh)) 
              rotate(180deg);
  width: 40px;
  transition: width 0.6s ease-in, 
              -webkit-transform 0.6s ease-in;
  transition: width 0.6s ease-in, 
              transform 0.6s ease-in;
  transition: width 0.6s ease-in, 
              transform 0.6s ease-in, 
              -webkit-transform 0.6s ease-in
}

.page-enter-active .landing.item.logo {
  -webkit-transform: none;
          transform: none;
  width: 30vmin;
}
